nav {
  display: flex;
  justify-content: center;
  text-transform: uppercase;
  font-size: 0.6rem;
  font-weight: 600;
  letter-spacing: 3px;
  padding: 1.5rem; }
  nav a:not(:last-child) {
    margin-right: 2rem; }
  nav a {
    cursor: pointer;
    display: inline-block;
    color: white;
    text-decoration: none;
    position: relative; }
    nav a:before {
      -webkit-transition: 0.5s all ease;
      transition: 0.5s all ease;
      position: absolute;
      left: 50%;
      right: 50%;
      bottom: -10px;
      opacity: 0;
      content: "";
      height: 2px;
      background-color: #c0b2fc; }
    nav a:hover {
      color: #c0b2fc; }
      nav a:hover:before {
        -webkit-transition: 0.5s all ease;
        transition: 0.5s all ease;
        left: 0;
        right: 0;
        opacity: 1; }
    nav a.selected {
      color: #c0b2fc; }
  nav.fixed {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
    background-color: #27263ff2;
    -webkit-transition: 0.3s;
    transition: 0.3s; }



.topbar{
